* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 30px; } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  background: rgb( 0, 0, 0.6);
  height: 84px;

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 13px;
    opacity: 0.5; }

  .subheader_call {
    color: #FFFFFF;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #FFFFFF;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    position: absolute;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  padding: 150px 0 135px 0;
  min-height: 800px;
  background: url('../img/bg/main_bg.jpg') center (center / cover) no-repeat;

  .promo_header {
    text-align: center;
    font-weight: 700;
    font-size: 60px;
    color: #FFFFFF;
    text-transform: uppercase;
    line-height: 60px;
    margin: 0; }

  .promo_subheader {
    text-align: center;
    margin-top: 22px;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    color: #FFFFFF;
    text-transform: uppercase; }

  .promo_descr {
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    margin: 116px auto 0 auto;
    display: block;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  text-align: center;
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  &_white {
    background-color: #ffffff;
    color: #000000; } }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  &_white {
    color: white; } }

.subtitle {
  margin-top: 1px;
  font-weight: 400;
  font-size: 17px;
  align-items: center;
  text-align: center;
  color: #222222;
  &_white {
    color: white; } }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }
.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/bg/mobile_bg.png') center (center / cover) no-repeat;
  .row {
    margin-top: 32px;
    margin-bottom: 12px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/bg/mobile_screen_1.png') center (center / cover) no-repeat; }
    &_2 {
      background: url('../img/bg/mobile_screen_2.png') center (center / cover) no-repeat; }
    &_3 {
      background: url('../img/bg/mobile_screen_3.png') center (center / cover) no-repeat; }
    &_4 {
      background: url('../img/bg/mobile_screen_4.png') center (center / cover) no-repeat; }
    &_5 {
      background: url('../img/bg/mobile_screen_5.png') center (center / cover) no-repeat; }
    &_6 {
      background: url('../img/bg/mobile_screen_6.png') center (center / cover) no-repeat; }
    &_subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF; }
    &_plus {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      bottom: 35px;
      left: 50%;
      transform: translate(-50%);
      width: 29px;
      height: 29px;
      background: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer; } } }
.car {
  padding: 59px 0 119px 0;
  background: url('../img/bg/car_bg.png') center (center / cover) no-repeat;
  min-height: 898px;
  &_item {
    padding: 40px;
    align-items: center;
    margin: 0 auto; }
  &_text {
    padding: 11px;
    text-align: center;
    margin: 0 auto;
    font-weight: 300;
    width: 327px;
    height: 49px;
    font-size: 17px;
    line-height: 24px;
    color: #222222; } }



